import React, { Component } from 'react'
import { connect } from 'react-redux';
import logo from '../assets/images/log/logo.jpg'
import './navbar.css'
import { Link } from 'react-router-dom';

class Navbar extends Component{


    componentDidMount(){
        document.querySelector('.navbar_wrapper').addEventListener('click',()=>{
            console.log("clicked")
            document.querySelector('#check').checked = false
        })

    }
    change(text){
        document.getElementById('check').checked = false
    }
  render(){
    return (
        <div>
            
                <input type="checkbox" id="check" />
                <label className="cancel" htmlFor="check"><i name="hide" className="fas fa-arrow-left"></i></label>
                <label className="show" htmlFor="check"><i  className="fas fa-bars"></i></label>
                <div className="navbar_wrapper"></div>
                <span className="gera_setting"> <Link to='setting'><i className="fas fa-cog"></i></Link></span>
            <nav className="navbar">
                <header>
                    <img src={logo} alt="muslip app"/>
                    <h1> Muslim App </h1> 
                </header>
                <ul>
                    <li onClick={(e)=> this.change(e.target.dataset.screen)}data-screen="home" name="home" >
                        <Link to="/"><i className="fas fa-home"></i>Accueil</Link>
                        
                    </li>
                    <li onClick={(e)=> this.change(e.target.dataset.screen)}data-screen="calendar" name="calendar" >
                        <Link to="/calendar"><i className="fas fa-calendar"></i>Horaires du mois</Link>
                        
                    </li>
                    <li onClick={(e)=> this.change(e.target.dataset.screen)}data-screen="coran" name="coran" >
                        <Link to="coran"><i className="fas fa-book"></i>Coran</Link>
                        
                    </li>
                    <li onClick={(e)=> this.change(e.target.dataset.screen)}data-screen="mosquee" name="masjid" >
                        <Link to="/direction"><i className="fas fa-map-marked-alt"></i>Lieux de priere</Link>
                        
                    </li>
                    <li onClick={(e)=> this.change(e.target.dataset.screen)}data-screen="qibla" name="setting" >
                        <Link to="/qiblah"><i className="fas fa-location-arrow"></i>Direction Queblah</Link>
                        
                    </li>
                    <li onClick={(e)=> this.change(e.target.dataset.screen)}data-screen="setting" name="setting" >
                        <Link to="/setting"><i className="fas fa-cog"></i>Setting</Link>
                        
                    </li>
                    <li onClick={(e)=> this.change(e.target.dataset.screen)}data-screen="contact" name="contact" >
                        <Link to="/contact"> <i className="fas fa-envelope"></i>Contact</Link>
                       
                    </li>
                </ul>
            </nav>
        </div>
    )
  }
}

const mapStateToProps = state => ({
    navbarShow: state.layout.navbarShow
})
export default connect(mapStateToProps)(Navbar)
