import React, { Component } from 'react'
import './HomePage.css'
import Priere from '../service/Priere'
import PriereTuile from '../components/PriereTuile'
import PriereRow from '../components/PriereRow'
import { getDate } from "../helpers/utils";
import CoranTuile from '../components/CoranTuile'
import {getPrieresDataFromApi, getHijri} from '../models/PrieresDatasFromApi'

export default class HomePage extends Component {
    constructor(props){
        super(props);
        this.state={
            lastAndFuturePriere: "",
            date_arabe: "",
            pret: false,
            priereDay:"",
            timer: "",
            date: "",
            day: new Date().getDate(),
            minutes : localStorage.getItem('adjust')
        }
        this.getParayer = this.getParayer.bind(this)
        this.giveDays = this.giveDays.bind(this)

    }
    componentWillMount(){
            console.log(this.state)
        this.setState({
            minutes: localStorage.getItem('adjust'),
            lastAndFuturePriere: Priere.getLastAndFuturePriere(getPrieresDataFromApi(localStorage.getItem('calcule_methode')), this.state.day),
            date_arabe: getHijri(localStorage.getItem('hijri_diff')),
            priereDay : Priere.getAllDayPriere(this.state.day),
            timer: Priere.getTimer(),
            date: getDate()

        })
       

    }

    tick(){
      
        if(!this.state.minutes === localStorage.getItem('adjust'))
            {  console.log('tick')
                this.setState({
                    lastAndFuturePriere: Priere.getLastAndFuturePriere(getPrieresDataFromApi(localStorage.getItem('calcule_methode')), this.state.day),
                    date_arabe: getHijri(localStorage.getItem('hijri_diff')),
                    priereDay : Priere.getAllDayPriere(this.state.day),
                    timer: Priere.getTimer(),
                    date: getDate()
        
                })
            }
    }

    componentDidMount(){
        this.setState({
            lastAndFuturePriere: Priere.getLastAndFuturePriere(getPrieresDataFromApi(localStorage.getItem('calcule_methode')), this.state.day),
            date_arabe: getHijri(localStorage.getItem('hijri_diff')),
            priereDay : Priere.getAllDayPriere(this.state.day),
            timer: Priere.getTimer(),
            date: getDate()
        })
        this.intervalID = setInterval(() => {
            this.tick()
        }, 500);
        }
        
        getParayer(){
            this.setState({
                lastAndFuturePriere: Priere.getLastAndFuturePriere(getPrieresDataFromApi(localStorage.getItem('calcule_methode')), this.state.day),
                timer: Priere.getTimer()
            })
        }

        componentWillUnmount() {
                clearInterval(this.timerID2);
                clearInterval(this.intervalID);
            }
        downloading(data){
            this.setState({
                pret: data
            })
        }

        giveDays(e){
            let date = this.state.day
            if(e.target.classList.contains('left')){
                date--;
                if(date < 0 ) date = 0
                this.setState({
                day: date,
                lastAndFuturePriere: Priere.getLastAndFuturePriere(localStorage.getItem('calcule_methode'),date),
                date_arabe: getHijri(localStorage.getItem('hijri_diff')),
                priereDay : Priere.getAllDayPriere(date),
                date: getDate()
            })
                
            }
            if(e.target.classList.contains('right')){
                
                date++;
                let today = new Date();
                let lastDayOfMonth = new Date(today.getFullYear(), today.getMonth()+1, 0).getDate();
                
                if(date >= lastDayOfMonth - 1) date = lastDayOfMonth -1 
               
                this.setState({
                    day: date,
                    lastAndFuturePriere: Priere.getLastAndFuturePriere(localStorage.getItem('calcule_methode'),date),
                    date_arabe: getHijri(localStorage.getItem('hijri_diff')),
                    priereDay : Priere.getAllDayPriere(date),
                    timer: Priere.getTimer(),
                    date: getDate()
                 })
            }
        }
    render() {
        return (
            this.state.lastAndFuturePriere && this.state.date_arabe && this.state.timer ?  
            <div className="home">
                <div className="hidden">
                    {this.props.minutes}{this.props.hijri}
                </div>
                <div className="tuile">
                    <PriereTuile />
                </div>
                <p className="date">{this.state.date_arabe}</p>
                <div className="listPrayer">
                    <PriereRow curent={this.state.lastAndFuturePriere} date={this.state.day}  priere={this.state.priereDay.fajr}/>
                    <PriereRow curent={this.state.lastAndFuturePriere} date={this.state.day}  priere={this.state.priereDay.sunrise}/>
                    <PriereRow curent={this.state.lastAndFuturePriere} date={this.state.day}  priere={this.state.priereDay.dhuhr}/>
                    <PriereRow curent={this.state.lastAndFuturePriere} date={this.state.day}  priere={this.state.priereDay.asr}/>
                    <PriereRow curent={this.state.lastAndFuturePriere} date={this.state.day}  priere={this.state.priereDay.maghrib}/>
                    <PriereRow curent={this.state.lastAndFuturePriere} date={this.state.day}  priere={this.state.priereDay.isha}/>
                </div>
                <div className="coran">
                    <CoranTuile/>
                </div>
            </div>
                :
                <div className="chargementDiv">
                    <i className="material-icons chargement">
                        replay
                    </i>
                </div>
        )
    }
}
