import React, { Component } from 'react';
import Navbar from './components/Navbar'
import './components/navbar.css'

import './App.css';
import { connect } from 'react-redux';
import HomePage from './pages/HomePage';
import SettingPage from './pages/SettingPage';
import TopBar from './components/TopBar';
import FirstScreen from './FirstScreen/FirstScreen'
import CoranPage from './pages/CoranPage';
import ContactPage from './pages/contact/ContactPage';
import CalendarMonth from './pages/CalendarMonth';
import QiblahPage from './pages/QiblahPage';

import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";


class  App extends Component {
  constructor(props){
    super(props)
    this.change = this.change.bind(this)
    this.changeScreen = this.changeScreen.bind(this)
    this.change_minutes_hijri = this.change_minutes_hijri.bind(this)
    if(localStorage.getItem('first') === null) localStorage.setItem('first', true)
    
  }
  componentDidMount(){
    if(localStorage.getItem('firstLoad') !== null && localStorage.getItem('firstLoad') === 'false'){
      this.props.dispatch({
        type: "CHANGE_SCREEN",
        preload :{screen: "home"}
      })
    }
  }

  render(){
    
    return (
    localStorage.getItem('first') === "true" ?  
    <FirstScreen change={this.change}/> : 
    <div className="App">
      <Router>
        <Navbar change={this.changeScreen}/>
        <TopBar screen={this.props.layout.screen}
                appName={this.props.layout.appName}/>
        <div className="main">
          <Switch>
            <Route exact path="/">
              <HomePage />
            </Route>
            <Route path="/setting">
              <SettingPage />
            </Route>
            <Route path="/coran">
              <CoranPage />
            </Route>
            <Route path="/contact">
              <ContactPage />
            </Route>
            <Route path="/qiblah">
              <QiblahPage />
            </Route>
            <Route path="/calendar">
              <CalendarMonth />
            </Route>
          </Switch>
        </div>
     </Router>
    </div>
  );
  }
  change(text){
     this.props.dispatch({
      type: "CHANGE_FirstLoad",
      preload :true
    })
    this.props.dispatch({
      type: "CHANGE_SCREEN",
      preload :{screen: text}
    })
  }
  change_minutes_hijri(){
    this.props.dispatch({
      type: "CHANGE_MINUTES",
      preload :{
        minutes: localStorage.getItem('adjust'),
        hijri: localStorage.getItem('hijri_diff')
      }
    })
  }

  changeScreen(text){
    this.props.dispatch({
      type: "CHANGE_SCREEN",
      preload :{screen: text}
    })
  }
 
} 
const mapProps = state =>{
  return state;
}

export default connect(mapProps)(App);
