import React,{useState, useEffect} from 'react'
import './CoranPage.css'

export default function CoranPage(props) {
    window.screenY = 0
    let surah_name = "";
   let page = "";
   
    const [coranPage, setcoranPage] = useState(null)
    const [nmbre, setnmbre] = useState(1)
    useEffect(()  => {
        fetch('http://api.alquran.cloud/v1/page/'+ nmbre +'/quran-uthmani')
        .then(data => data.json())
        .then(coranData =>{
            setcoranPage(coranData) 
        })
    }, [nmbre])

    return (
        <div className="coranPage">
            {coranPage === null ? <div>Loading</div>:
            <div >
                {surah_name = coranPage.data.ayahs[0].name}
                {coranPage.data.ayahs.map((ayah)=>{
                
                    if(surah_name !== ayah.surah.name){
                        page = <div key={ayah.number}> <p className="surah_name">﴾ {ayah.surah.name} ﴿</p> 
                                    <span>﴾{ayah.number}﴿</span>
                                    <p className="coranAyah">{ayah.text}</p> 
                                </div>
                        surah_name = ayah.surah.name
                    }
                    else{page = <div key={ayah.number}>
                                    <span>﴾{ayah.number}﴿</span>
                                    <p className="coranAyah">{ayah.text}</p> 
                                </div>
                    }
                    return page
                })}
            </div>
            }
            <div className="coran_nav">
                <button className="button_left" ><i onClick={()=>setnmbre(nmbre+1)}data-nmbre="plus" className="fas fa-arrow-left"></i></button>
                <span> Pages </span>
                {nmbre > 1 &&  <button className="button_right"><i onClick={()=>setnmbre(nmbre-1)} data-nmbre="moins" className="fas fa-arrow-right"></i></button>}
               
            </div>

        </div>
        
    )
}


