import React from 'react'
import './form.css'

import {verifInput, verifForm, verifInput_} from '../../utils/form'

export default function Form() {
    return (
            <form className="contact_form" method="POST" action="./sendMail.php">
                <div>
                    <label htmlFor="name">Nom, Prenom : </label>
                    <input id="name" name="name" placeholder="Jean, Delouis" onBlur={(event)=>verifInput(event)} onFocus={(event)=>verifInput_(event)}/>
                    <div className="alert-warning"></div> 
                </div>
                <div>
                    <label htmlFor="mail">Email : </label>
                    <input id="mail" name="mail" placeholder="Jean.Delouis@exemple.com" onBlur={(event)=>verifInput(event)} onFocus={(event)=>verifInput_(event)}/>
                    <small>Votre email n'est ni sauvgradé ni partagé, il nous serviras pour vous répondre.</small>
                    <div className="alert-warning"></div> 
                </div>
                <div>
                    <label htmlFor="phone">Téléphone : </label>
                    <input id="phone" name="phone" placeholder="(3+3) 06 01234567" onBlur={(event)=>verifInput(event)} onFocus={(event)=>verifInput_(event)}/>
                    <div className="alert-warning"></div> 
                </div>
                <div>
                    <label htmlFor="message">Message : </label>
                    <textarea id="message" name="message" rows="8" placeholder="Votre message..." onBlur={(event)=>verifInput(event)} onFocus={(event)=>verifInput_(event)}></textarea>
                    <div className="alert-warning"></div> 
                </div>
                <div className="form-submit">
                    <input type="submit" id="submit" name="submit" value="Envoyer" onClick={(event)=>verifForm(event)}/>
                </div>
            </form>
    )
}

