import React, { Component } from 'react';
import {capitalise} from '../helpers/utils';
import { Link } from 'react-router-dom';


export default class SettingPage extends Component {

    constructor(props){
        super(props);
        this.state = {
            longitude: "",
            latitude: "",
            geo_on: false,
            reglage_auto : true,
            ville: "",
            pays: "",
            methode: "",
            auto: false,
            show_ville : true,
            fajr_range : 0,
            shorouk_range : 0,
            dhohr_range : 0,
            asr_range : 0,
            maghrib_range : 0,
            isha_range : 0,
            hijri_diff: 0,
            }
        
            if(localStorage.getItem('ville') !== null) this.state.ville = localStorage.getItem('ville')
            if(localStorage.getItem('pays') !== null) this.state.pays = localStorage.getItem('pays')
            if(localStorage.getItem('cp') !== null) this.state.cp = localStorage.getItem('cp')
            if(localStorage.getItem('latitude') !== null) this.state.latitude = localStorage.getItem('latitude')
            if(localStorage.getItem('longitude') !== null) this.state.longitude = localStorage.getItem('longitude')
            if(localStorage.getItem('hijri_diff') !== null) this.state.hijri_diff = localStorage.getItem('hijri_diff')
            if(localStorage.getItem('fajr') !== null) this.state.fajr_range = localStorage.getItem('fajr')
            if(localStorage.getItem('shorouk') !== null) this.state.shorouk_range = localStorage.getItem('shorouk')
            if(localStorage.getItem('dhuhr') !== null) this.state.dhohr_range = localStorage.getItem('dhuhr')
            if(localStorage.getItem('asr') !== null) this.state.asr_range = localStorage.getItem('asr')
            if(localStorage.getItem('maghrib') !== null) this.state.maghrib_range = localStorage.getItem('maghrib')
            if(localStorage.getItem('isha') !== null) this.state.isha_range = localStorage.getItem('isha')
            

        this.getLocation = this.getLocation.bind(this);
        this.auto = this.auto.bind(this);
        this.change = this.change.bind(this);
        this.change_ = this.change_.bind(this);
        this.valide_setting = this.valide_setting.bind(this)
        this.setting = this.setting.bind(this)
        this.switcher = "";
    } 

    componentDidMount(){
      this.switcher_location = document.querySelector('.switcher');
      this.switcher_location2 = document.querySelector('.switcher2');
      if(localStorage.getItem('adjust') === null ){
        localStorage.setItem('adjust', "0 0 0 0 0 0 0 0 0")
      }
      if(localStorage.getItem('ville') !== null) this.setState({ville : localStorage.getItem('ville')})
      if(localStorage.getItem('pays') !== null) this.setState({pays : localStorage.getItem('pays')})
      if(localStorage.getItem('cp') !== null) this.setState({cp : localStorage.getItem('cp')})
      if(localStorage.getItem('latitude') !== null) this.setState({latitude : localStorage.getItem('latitude')})
      if(localStorage.getItem('longitude') !== null) this.setState({longitude : localStorage.getItem('longitude')})
      if(localStorage.getItem('hijri_diff') !== null) this.setState({hijri_diff : localStorage.getItem('hijri_diff')})
      if(localStorage.getItem('fajr') !== null) this.setState({fajr_range : localStorage.getItem('fajr')})
      if(localStorage.getItem('shorouk') !== null) this.setState({shorouk_range : localStorage.getItem('shorouk')})
      if(localStorage.getItem('dhuhr') !== null) this.setState({dhohr_range : localStorage.getItem('dhuhr')})
      if(localStorage.getItem('asr') !== null) this.setState({asr_range : localStorage.getItem('asr')})
      if(localStorage.getItem('maghrib') !== null) this.setState({maghrib_range : localStorage.getItem('maghrib')})
      if(localStorage.getItem('isha') !== null) this.setState({isha_range : localStorage.getItem('isha')})



    }


    getLocation(){
      if(this.state.geo_on === false){
        this.switcher_location = document.querySelector('.switcher');
          this.switcher_location.style.justifyContent = "flex-end"
          this.switcher_location.style.backgroundColor = "rgb(14, 168, 47)"  
          this.setState({
            geo_on : true,

          })
          localStorage.setItem('geo_on' ,true)
            navigator.geolocation.getCurrentPosition((position)=>{
              localStorage.setItem('latitude', position.coords.latitude)
              localStorage.setItem('longitude', position.coords.longitude)
              fetch(`http://nominatim.openstreetmap.org/reverse?format=json&lat=${position.coords.latitude}&lon=${position.coords.longitude}&zoom=18&addressdetails=2`)
              .then(datas => datas.json()).then(
                      datas => {
                        localStorage.setItem('ville', datas.address.village)
                        localStorage.setItem('pays', datas.address.country)
                        this.setState({
                          ville: datas.address.village,
                          pays: datas.address.country
                        })
                      }
                    )
              this.setState({
                longitude: position.coords.longitude,
                latitude: position.coords.latitude,
                geo_on : true, 
              })
            },
            (err) => console.log(err)
            )
          
      }else{
        localStorage.setItem('geo_on' ,false)
        this.switcher_location = document.querySelector('.switcher');
        this.switcher_location.style.justifyContent = "flex-start"
        this.switcher_location.style.backgroundColor = "#a1a1a1" 
        if(localStorage.getItem("latitude")){
          localStorage.removeItem('latitude')
          localStorage.removeItem('longitude')
        }
        this.setState({
          geo_on : false,
          show_ville : true,
        })
      }

     
    }

    auto(){
      if(this.state.reglage_auto === true ){
        this.switcher_location2 = document.querySelector('.switcher2');
        this.switcher_location2.style.justifyContent = "flex-start"
        this.switcher_location2.style.backgroundColor = "#a1a1a1"
        
        localStorage.setItem('reglage_auto', false)
        this.setState({
          reglage_auto : false,
         })
     }else{ 

      this.switcher_location2 = document.querySelector('.switcher2');
       this.switcher_location2.style.justifyContent = "flex-end"
         this.switcher_location2.style.backgroundColor = "rgb(14, 168, 47)" 
         localStorage.setItem('reglage_auto', true)
       this.setState({
        reglage_auto : true,
       })
     }
    }
    change(e){
      this.getPriere()
      this.setState({
        error: ""
      })
      let{ name, value } = e.target
      value = capitalise(value)
      this.setState({
        [name]: value
      })

      localStorage.setItem(name, value)
    }
    change_(){

      //https://nominatim.openstreetmap.org/search/ormes,45140?format=json&addressdetails=1&limit=1
      fetch(`https://nominatim.openstreetmap.org/search/${localStorage.getItem('ville') +','+ localStorage.getItem('cp')+','+localStorage.getItem('pays')}?format=json&addressdetails=1&limit=1`)
      .then(datas => datas.json())
      .then(datas => {
        console.log(datas)
                localStorage.removeItem('latitude')
                localStorage.removeItem('longitude')
                localStorage.setItem('latitude', datas[0].lat)
                localStorage.setItem('longitude', datas[0].lon)
              }
            )
    }

    getPriere(){
        let adjust = ""+localStorage.getItem('fajr_range')
                      + " " + localStorage.getItem('shorouk_range')
                      + " " + localStorage.getItem('dhohr_range')
                      + " " + localStorage.getItem('asr_range')
                      + " " + localStorage.getItem('maghrib_range')
                      + " " + localStorage.getItem('isha_range')
          localStorage.setItem('adjust',adjust) 
          localStorage.setItem('hijri_diff', this.state.hijri_diff)
    }

    valide_setting(){
        let lat = localStorage.getItem('latitude');
        let lon = localStorage.getItem('longitude');
        fetch(`http://nominatim.openstreetmap.org/reverse?format=json&lat=${lat}&lon=${lon}&zoom=18&addressdetails=2`)
      .then(datas => datas.json())
      .catch(err => console.log(err))
    }
    setting(){
      this.getPriere()

    }

    render() {
        return (
            <div className="setting_"> 
              {this.state.error && <div className="alert alert-warning">{this.state.error}</div>}
              
              <div>
                <h4 className="setting_section">Votre Localisation :</h4>
                <div onClick={ this.getLocation } className=" form-group geolocation">
                
                  <label htmlFor="switch-label">Localisez-moi</label>
                  <div className="switcher" onClick={ this.getLocation }><i className="fas fa-check-circle"></i></div>
                </div>
                {localStorage.getItem('geo_on') === 'true' && 
                  
                  <div className="adresse_details">
                    <p >
                      Pays : <strong>{this.state.pays} </strong>
                      </p>
                      <p>
                      Ville : <strong>{this.state.ville} </strong>
                      </p>
                      <p>
                      Code postale : <strong>{this.state.cp}</strong>
                    </p>
                  </div>
                }
                {localStorage.getItem('geo_on') === 'false'  && 
                <div>
                  <div className="form-group">
                    <label htmlFor="country">Pays :</label>      
                    <input className="form-control" name="pays" value={this.state.pays} onChange={(e) => this.change(e)} />
                  </div>
                  <div className="form-group">
                    <label htmlFor="country">Ville :</label>  
                    <input className="form-control" name="ville" value={this.state.ville} onChange={(e) => this.change(e)} />
                  </div>
                  <div className="form-group">
                      <label htmlFor="country">Code postale :</label>  
                      <input className="form-control" name="cp" value={this.state.cp} onChange={(e) => this.change(e)}/>
                  </div>
                  <div>
                      <button className="btn btn-success valider-setting " onClick={()=>this.change_()}>Valider</button>
                  </div>
                </div>
              }
              </div>

                <hr />
                <h4 className="setting_section">Methode de calcule :</h4>
              <div onClick={ this.auto } className="geolocation auto">
                <label htmlFor="switch-label">Reglage auto</label>
                <div className="switcher2" onClick={ this.auto }><i className="fas fa-check-circle"></i></div>
              </div>
              <hr />
              <div>
                <div className="form-group">
                  <label htmlFor="country">Methode de calcule :</label>  
                  <select className="form-control" name="calcule_methode" value={this.state.methode} onFocus={(e)=>this.change(e)} onChange={(e) => this.change(e)} >
                    <option value="MWL">Muslim World League (MWL)</option>
                    <option value="ISNA">Islamic Society of North America (ISNA)</option>
                    <option value="Egypt">Egyptian General Authority of Survey</option>
                    <option value="Makkah">Umm al-Qura University, Makkah</option>
                    <option value="Karachi">University of Islamic Sciences, Karachi</option>
                    <option value="Jafari">Shia Ithna-Ashari (Jafari)</option>
                    <option value="Tehran">Institute of Geophysics, University of Tehran</option>
                  </select>
                </div>
              </div>
              <hr />
              <div className="minutes">
                <h4 className="setting_section">Ajuster la date hijri :</h4>
                <div className="range">
                  <label>Date hijri : {localStorage.getItem('hijri_diff')} jours</label>
                  <div className="range_field">
                      <span>-2 jours</span> <input onChange={(e)=>this.change(e)}  name="hijri_diff"
                      type="range" min="-2" max="2" step="1" 
                      title="Fajr" value={localStorage.getItem('hijri_diff')}/><span>+2 jours</span>
                  </div>
                </div>
              </div>
              <hr />
              <div className="minutes">
                <h4 className="setting_section">Ajuster les minutes des prieres :</h4>
                <div className="range">
                  <label>Fajr : {localStorage.getItem('fajr_range')} minutes</label>
                  <div className="range_field">
                      <span>-10 min</span> <input  onChange={(e)=>this.change(e)} name="fajr_range" 
                      type="range" min="-10" max="10" step="1" 
                      title="Fajr" value={localStorage.getItem('fajr_range')}/><span>+10 min</span>
                  </div>
                </div>

                <div className="range">
                  <label>Shorouk : {localStorage.getItem('shorouk_range')} minutes</label>
                  <div className="range_field">
                      <span>-10 min</span> <input  onChange={(e)=>this.change(e)} name="shorouk_range" 
                      type="range" min="-10" max="10" step="1" 
                      title="shorouk" value={localStorage.getItem('shorouk_range')}/><span>+10 min</span>
                  </div>
                </div>

                <div className="range">
                  <label>Dhohr : {localStorage.getItem('dhohr_range')} minutes</label>
                  <div className="range_field">
                      <span>-10 min</span> <input onChange={(e)=>this.change(e)} name="dhohr_range" 
                      type="range" min="-10" max="10" step="1" 
                      title="dhohr" value={localStorage.getItem('dhohr_range')}/><span>+10 min</span>
                  </div>
                </div>

                <div className="range">
                  <label>Asr : {localStorage.getItem('asr_range')} minutes</label>
                  <div className="range_field">
                      <span>-10 min</span> <input onChange={(e)=>this.change(e)} name="asr_range" 
                      type="range" min="-10" max="10" step="1" 
                      title="asr" value={localStorage.getItem('asr_range')}/><span>+10 min</span>
                  </div>
                </div>
                <div className="range">
                  <label>Maghrib : {localStorage.getItem('maghrib_range')} minutes</label>
                  <div className="range_field">
                      <span>-10 min</span> <input onChange={(e)=>this.change(e)} name="maghrib_range" 
                      type="range" min="-10" max="10" step="1" 
                      title="maghrib" value={localStorage.getItem('maghrib_range')}/><span>+10 min</span>
                  </div>
                </div>

                <div className="range">
                  <label>Isha : {localStorage.getItem('isha_range')} minutes</label>
                  <div className="range_field">
                      <span>-10 min</span> <input onChange={(e)=>this.change(e)} name="isha_range" 
                      type="range" min="-10" max="10" step="1" 
                      title="isha" value={localStorage.getItem('isha_range')}/><span>+10 min</span>
                  </div>
                </div>
              </div> 
              <hr />
              <div>
                <button className="valider_setting" onClick={()=>this.setting()}><Link to="/">Valider</Link>  </button>
              </div>
            </div>
        );
      }
      
}
    

