import PrayTimes from '../service/PrayTimes'
import HijriDate from  '../service/hijriCalendar'
import {jours, mois} from './ar'

function getPrieresDataFromApi(methode = localStorage.getItem('calcule_methode'), date = new Date(), coords = [localStorage.getItem('latitude'), localStorage.getItem('longitude')])
    {
        
        let prayTimes = new PrayTimes(methode)
        prayTimes.tune({
            fajr: localStorage.getItem('fajr_range'),
            sunrise: localStorage.getItem('shorouk_range'),
            dhuhr: localStorage.getItem('dhuhr_range'),
            asr: localStorage.getItem('asr_range'),
            maghrib: localStorage.getItem('maghrib_range'),
            isha: localStorage.getItem('isha_range'),
        })
        let prieres =  prayTimes.getTimes(date, coords)
                return prieres
           
}
let hijri = new HijriDate()
function getHijri(diff = 0){
    
    let dateHijri = jours[hijri.getDay()] + " " + (1* parseInt(hijri.getDate())+ 1* diff) + " " +mois[hijri.getMonth()+1] + " " + hijri.getFullYear()
   
    return dateHijri
}
function daysInMonth (month, year) {
    return new Date(year, month, 0).getDate();
}

