import React from 'react'
import './CoranTuile.css'

export default function CoranTuile(props) { 
   
    let surah = Math.floor(Math.random() * 114)
    if(surah === 0){
        surah = surah + 1
    }
    let datas = require('../assets/surah/surah_'+ surah + '.json')
    let ayats = Math.floor( Math.random() *(datas.count + 1))


    return (
    <div className="aya">
           <h4>الْقُرْآن الْكَرِيْم</h4>
            <h6>{datas.name}</h6>  
             <p>{datas.verse["verse_" + ayats ]}</p>   
        </div>
    )
}
