import React, { Component } from 'react';
import './priereRow.css';

export default class PriereRow extends Component{

    constructor(props){
        super(props);
        this.state={
            priere: "",
            curentPray:"",
        }
    }

    componentDidMount(){
        if (this.props.curent.last.time === this.props.priere.time && this.props.date === new Date().getDate()){
           this.cure = "curent"
        }
        else{
            this.cure="";
        }
    
        this.setState({
            priere: this.props.priere,
            curentPray: this.cure,
            date: this.props.date   
        })

    }
    componentWillReceiveProps(props) {
        if (props.curent.last.time === props.priere.time && props.date === new Date().getDate()){
            this.cure = "curent"
         }
         else{
             this.cure="";
         }
        this.setState({
            priere: props.priere,
            curentPray: this.cure,
            date: props.date   
        })  
      }

    render(){
        return(
            <div className={"priereRow " + this.state.curentPray}>
                <h5 className="left" >{this.state.priere.name_fr}</h5>
                <h5 className="center">{this.state.priere.time}</h5>
                <h5 className="right">{this.state.priere.name_ar}</h5>
            </div>
        )
    }



}