import React,{Component} from 'react';
import { salato } from '../models/ar.js';
import './PriereTuile.css';
import athan from '../assets/audio/athans/a7.mp3';
import Priere from '../service/Priere.js'; 
import { getPrieresDataFromApi, getHijri } from '../models/PrieresDatasFromApi.js';

class  PriereTuile extends Component{
    interval_id;
    constructor(props){
        super(props);
        this.state={
            time : "",
            curentPriere: "",
            futurePriere: "",
            date_arabe: "",
            r: "",
            width: "",
            inistialOffset: 0,
            offset: 0,
            athan: athan
        }
    }
     componentDidMount() {
      
        document.querySelector('.audio').source={athan}
        this.setState({
          curentPriere: Priere.getLastAndFuturePriere(getPrieresDataFromApi()).last,
          futurePriere: Priere.getLastAndFuturePriere(getPrieresDataFromApi()).future,
          date_arabe: getHijri(),
          time: Priere.getTimer(getPrieresDataFromApi()).time_,
          
       })
      
        this.timerID = setInterval(
                () => this.tick(),
                1000
              );


        if(this.state.time !== ""){
              let timeToFuture = this.state.futurePriere.time.split(":");
              let toNextInSeconde = (parseInt(timeToFuture[0])) * 60 * 60 + (parseInt(timeToFuture[1])) * 59  ;

              let timelast = this.state.curentPriere.last.time.split(":");
              let lastInSeconde = (parseInt(timelast[0])) * 60 * 60 + (parseInt(timelast[1])) * 59 ;
              let diffBetween = toNextInSeconde - lastInSeconde
              let r = Math.floor(diffBetween / (Math.PI))

              this.setState({
                r: r
              })

              let circle = document.querySelector('.circle');
              circle.style.stroke =  "teal";
              
              circle.style.strokeDasharray = 188
              circle.style.strokeDashoffset =  this.state.inistialOffset
              
        }
      }
      componentWillUnmount() {
        clearInterval(this.timerID);
        clearInterval(this.timerID2);
      }

      format(num){
          return num > 9 ? num : "0" + num
      }
    
      tick() { 
        
        var diff;

        let newDate = new Date()
        
        let timeToFuture = this.state.futurePriere.time.split(":");
        let toNextInSeconde = (parseInt(timeToFuture[0])) * 3600 + (parseInt(timeToFuture[1])) * 60 + 60;
        let timeInSeconde = newDate.getHours() * 3600 +  newDate.getMinutes() * 60 + newDate.getSeconds() ;


        let timelast = this.state.curentPriere.time.split(":");
        let lastInSeconde = (parseInt(timelast[0])) * 60 * 60 + (parseInt(timelast[1])) * 60

        let diffBetween = toNextInSeconde - lastInSeconde
       

        //console.log('diffBetween ' + diffBetween)

        if(this.state.futurePriere.name === salato.Fajr){

            let ishaTime = this.state.curentPriere.time.split(":");
            let ishaInSecondes = (parseInt(ishaTime[0])) * 60 * 60 + (parseInt(ishaTime[1])) * 60 ;
      
            diffBetween = (86400 - ishaInSecondes ) + toNextInSeconde

            if(timeInSeconde > ishaInSecondes){
              diff =(86400 - timeInSeconde ) + toNextInSeconde;
                
            }
            else{
              diff =toNextInSeconde - timeInSeconde  ;
            }
        }
        else{
            diff =  toNextInSeconde - timeInSeconde;
        }

        
          this.setState({diff: diff})
          if(diff <= 10 ){
            console.log("this marche")
            this.setState({
                curentPriere: Priere.getLastAndFuturePriere(getPrieresDataFromApi()).last,
                futurePriere: Priere.getLastAndFuturePriere(getPrieresDataFromApi()).future,
                date_arabe: getHijri(),
                time: Priere.getTimer(getPrieresDataFromApi()).time_,
               
              })
            this.playAthan()
            
          }

        let time_ = this.format((Math.floor(diff / 3600))) + " : " + this.format((Math.floor((diff % 3600) / 60))) + " : " + this.format((diff % 3600 % 60))
        
        
        this.setState({
          inistialOffset:188 - (diff / diffBetween),
          offset: (diff / diffBetween) * 188,
          time: time_
        });
      

        let circle = document.querySelector('.circle');
        circle.style.strokeDasharray = 188
        circle.style.stroke =  "#46afb8";
        if(this.state.offset < 50){
          circle.style.stroke =  "#46afb8";
        }
        circle.style.strokeDashoffset =  Math.ceil(this.state.offset)
      }

      playAthan(){  
        document.querySelector('.audio').play();
      }
   
render(){
    return(
            
        //<div className="ctrl"> 
          <div className="progressCircle">
            <svg viewBox="0 0 100 100">
              <circle cx="50" cy="50" r="30" className="over" />
              <circle cx="50" cy="50" r="30" className="circle" />
            </svg>
            <div className="svg-text">
              <p>{this.state.futurePriere.name }</p>
              <p>{this.state.futurePriere.time }</p>
              <p>{this.state.time} </p>
            </div>
              <audio className="audio" muted="muted">
                <source src={this.state.athan} />
              </audio>
          </div>
                  
                  
            //       <p>{this.state.date_arabe }</p>
            //       <p className="priereName">{this.state.futurePriere.time }  {this.state.futurePriere.name }</p>
            //       <p> <span className="apres">بعد</span></p>
            //       <p className="timer">{this.state.time }</p>

            // </div>
        )    
    }
        
}


export default PriereTuile;