import initialState from '../state'


const layoutreducer = (state = initialState, action) =>{
    switch (action.type){
        case "CHANGE_SCREEN":
            return {...state, screen: action.preload.screen }
        case "FETCH_DATA":
            return  {...state, salat: action.preload.salat, download: true }
        case "TOGGLE_NAVBAR":
            return {...state, navbarShow: action.preload.navbarShow }
        case "CHANGE_FirstLoad":
            return {...state, firstLoad: action.preload}
        case "CHANGE_MINUTES":
            return {...state, minutes: action.preload.minutes, hijri: action.preload.hijri}

        default:
            return {...state}

    }
}

export default layoutreducer;