let direction = "NNE";
function radians_to_degrees(radians)
{
  let pi = Math.PI;
  return radians * (180/pi);
}

function degrees_to_radians(degrees)
{
  let pi = Math.PI;
  return degrees * (pi/180);
}

function getCardinaux(degrees) {
   let tmp = Math.round(degrees/ 22.5);
   switch(tmp) {
      case 1:
          direction = "NNE";
         break;
      case 2:
          direction = "NE";
         break;
      case 3:
          direction = "ENE";
         break;
      case 4:
          direction = "E";
         break;
      case 5:
          direction = "ESE";
         break;
      case 6:
          direction = "SE";
         break;
      case 7:
          direction = "SSE";
         break;
      case 8:
          direction = "S";
         break;
      case 9:
         direction = "SSW";
         break;
      case 10:
          direction = "SW";
         break;
      case 11:
          direction = "WSW";
         break;
      case 12:
          direction = "W";
         break;
      case 13:
          direction = "WNW";
         break;
      case 14:
          direction = "NW";
         break;
      case 15:
          direction = "NNW";
         break;
      default:
          direction = "N";
   }
   return direction;
}

function getDegree(lat, lon){
//get user coords
    let lat_depart = lat;
    let lng_depart = lon;
// meque qaaba coords
    let lat_arrivee =  39.826186;
    let lng_arrivee = 21.422524;	
//calcul degree 		
    let degres = (radians_to_degrees(Math.atan2(Math.sin(degrees_to_radians(lng_arrivee) 
    - degrees_to_radians(lng_depart)) 
    * Math.cos(degrees_to_radians(lat_arrivee)), Math.cos(degrees_to_radians(lat_depart)) * Math.sin(degrees_to_radians(lat_arrivee)) - 
    Math.sin(degrees_to_radians(lat_depart)) * Math.cos(degrees_to_radians(lat_arrivee)) * Math.cos(degrees_to_radians(lng_arrivee) 
    - degrees_to_radians(lng_depart)))) + 360) % 360;		
return  Math.ceil(degres * 100) / 100;
}

export default getDegree;
