import React, {Component } from 'react'
import './QiblahPage.css'
import getDirection from '../service/direction'
import Boussole from '../components/boussole'



export default class QiblahPage extends Component{
    constructor(props){
        super(props)
        this.state = {
            dir: "0deg"
        }
    }
    componentDidMount(){
      this.setState({ dir: 144 + Math.floor(getDirection(localStorage.getItem('latitude'), localStorage.getItem('longitude')))})

    }
   render(){
    
    return(
        localStorage.getItem('latitude') !== null ?
        <div className="qiblah">
            <h2>La direction de la Qiblah est : <span>{Math.floor(getDirection(localStorage.getItem('latitude'), localStorage.getItem('longitude')))} deg</span></h2>
            <Boussole transform={"rotate("+ this.state.dir + ")"}/>

        </div>
        :
        <div className="qiblah">
            <h4>
            Veuillez activer la localisation pour avoir la direction exacte
            </h4>
        </div>
    )
    }
}