import React, { Component } from "react";
import GetLocation from "./GetLocation";
import Bienvenue from "./Bienvenue";

export default class FirstScreen extends Component{

    constructor(props){
        super(props);
        this.state = {
            screen: 1,
            latitude: "",
            longitude: "",
            ville: "",
            pays: "",
            methode: ""

        }

        this.toNext = this.toNext.bind(this)
        this.change = this.change.bind(this)
    }

    toNext(){
        let screen_ = this.state.screen;
        screen_ += 1 ;
        this.setState({
            screen: screen_ 
        })
    }
    change(text){
        this.props.change(text)
    }

    render(){
       

        return( 
            <div className="wrapper">
                <Bienvenue  next={this.toNext} screen={this.state.screen}/>
                <GetLocation next={this.toNext} first={true} screen={this.state.screen} change={this.change}/>
            </div>
        )
    }
    

}

