import React from 'react';
import './bienvenue.css';
import logo from '../assets/images/log/logo.jpg'


export default function Bienvenue(props){

    if(props.screen !== 1) { return null}
    return(
        <div className="bienvenue">
            <h1 className="h-bienvenue">MUSLIM REMINDER</h1>
            <img src={logo} alt="muslim reminder"/>
            <button className="b-bienvenue" onClick={props.next}>Suivant</button>

        </div>
    )

}