let error = [];

const verifForm = (event) =>{
    if(error.length > 0){
        event.preventDefault()
    }
}
const verifInput_ = (event) =>{
        let alert = event.target.parentNode.querySelector('.alert-warning')
        alert.innerHTML = ""
        alert.style.display = "none"
    
}
const verifInput = (event) =>{
    let champName = event.target.name
    let champValue = event.target.value
    champValue === "" ? error[champName] = "Ce champs ne peut pas etre vide !": error[champName] = "";
    if(error !== ""){
        let alert = event.target.parentNode.querySelector('.alert-warning')
        alert.innerHTML = error[champName]
        alert.style.display = "block"
    }
}

export {verifForm, verifInput, verifInput_}