
 function  timeToNext(prieres){
    let priereTime = [];

    for(let priere in prieres){
        let a = prieres[priere].split(':'); // split it at the colons
        // minutes are worth 60 seconds. Hours are worth 60 minutes.
        let b = (parseInt(a[0])) * 60 * 60 + (+parseInt(a[1]) * 60); 
        priereTime.push(b)
    }

 

}
 function capitalise(string){
     let s = string.toLowerCase();
     let s0 = s.charAt(0);
     let S0 = s0.toUpperCase();

     return S0 + s.slice(1)
 }

 function getDate(){
     return new Date().getDate() + " " + (new Date().getMonth() +1 )  + " " +new Date().getFullYear()
 }


export  {timeToNext, capitalise, getDate};