import React, { Component } from 'react'
import './CalendarMonth.css'


export default class CalendarMonth extends Component {

    constructor(props){
        super(props)
        this.state={
            calendar : ""
        }
    }
    componentDidMount(){

        return fetch('http://api.aladhan.com/v1/calendarByCity?city=' + localStorage.getItem('ville') 
            +'&country='+ localStorage.getItem('pays') 
            +'&method='+ localStorage.getItem('calcule_methode')
            +'&month='+ (new Date().getMonth()+1)  
            +'&year='+ (new Date().getFullYear()) 
            +'&tune='+ localStorage.getItem('adjust'))
    .then(data_ => data_.json())  
    .then(
        datas =>{
            this.setState({calendar: datas.data})
        }
    )
        
    }
    render() {
        
        return (
            this.state.calendar === "" ? 
            <div className="calendar">Loading ...</div>:
            <div className="calendar">
                <h3>Calendier du mois de {this.state.calendar[0].date.gregorian.month.en}</h3>
                <table id="customers">
                    <thead>
                    <tr>
                        <th>{this.state.calendar[0].date.hijri.month.ar}</th>
                        <th>Fajr</th>
                        <th>Dohr</th>
                        <th> Asr</th>
                        <th>Maghrib</th>
                        <th> Isha</th>
                        <th>{this.state.calendar[0].date.gregorian.month.en}</th>
                    </tr>
                    </thead>
                    <tbody>
                {this.state.calendar.map(ele =>{
                    this.class_ = ele.date.gregorian.day === new Date().getDate() ? "curent_" : ""
                    return <tr key={ele.date.gregorian.day} className={this.class_}>
                                <td>{ele.date.hijri.day}</td>
                                <td>{ele.timings.Fajr.slice(0,5)}</td>
                                <td>{ele.timings.Dhuhr.slice(0,5)}</td>
                                <td>{ele.timings.Asr.slice(0,5)}</td>
                                <td>{ele.timings.Maghrib.slice(0,5)}</td>
                                <td>{ele.timings.Isha.slice(0,5)}</td>
                                <td>{ele.date.gregorian.day}</td>
                            </tr>
                })}
                </tbody>
                </table>
            </div>
            
        )
    }
}
