    static  getLastAndFuturePriere(salat, date = new Date().getDate()){   
          
            let prieresTimings = salat
            let priereTimesLeft = [];
            let priereTimesFuture = [];
            let dd = {dd: date}
            let isha = prieresTimings.isha.split(':'); // split it at the colons
            let fajr = prieresTimings.fajr.split(':'); // split it at the colons
                // minutes are worth 60 seconds. Hours are worth 60 minutes.
            let ishaInSeconds = (parseInt(isha[0])) * 60 * 60 + (+parseInt(isha[1]) * 60); 
            let fajrInSeconds = (parseInt(fajr[0])) * 60 * 60 + (+parseInt(fajr[1]) * 60); 
            let nowInSeconde = new Date().getHours() * 3600 +  new Date().getMinutes() * 60;
    
            if( nowInSeconde > ishaInSeconds || nowInSeconde < fajrInSeconds ){
                
                return  {
                    last : {name : salato.Isha, time: prieresTimings.isha.slice(0,5)},
                    future: {name : salato.Fajr, time: prieresTimings.fajr.slice(0,5)},
                }
            }
    
            for(let priere in prieresTimings){
                if(priere === "Sunset"){
                    continue
                }
                
                let a = prieresTimings[priere].split(':'); // split it at the colons
                // minutes are worth 60 seconds. Hours are worth 60 minutes.
                let b = (parseInt(a[0])) * 60 * 60 + (+parseInt(a[1]) * 60); 
               
               if(b < nowInSeconde)
               {
    
                priereTimesLeft.push({name: salato[priere], time: prieresTimings[priere].slice(0,5)})
               }
               else{
            
    
                priereTimesFuture.push({name: salato[priere], time: prieresTimings[priere].slice(0,5)})
               }
               
            }
            let last = priereTimesLeft[priereTimesLeft.length -3 ];
            let future = priereTimesFuture[0];
            
            return {
                last,
                future,
                dd
            }
        
        
       
    }
    
    /* return formated time */
    static format(num){
        return num > 9 ? num : "0" + num
    }

    /**
     * getTimer to next priere
     */

    static getTimer(){

        let salat_ = getPrieresDataFromApi()
        let last_future = this.getLastAndFuturePriere(salat_, new Date().getDate());
        let diff = 0;

        let timeToFuture = last_future.future.time.split(":");
        let toNextInSeconde = (parseInt(timeToFuture[0])) * 60 * 60 + (parseInt(timeToFuture[1])) * 60 + 60;
        let timeInSeconde = new Date().getHours() * 3600 +  new Date().getMinutes() * 60 + new Date().getSeconds();

        //console.log(timeToFuture ,timeInSeconde - toNextInSeconde)

        if(last_future.future.name === salato.Fajr){
            let ishaTime = last_future.last.time.split(":");
            let ishaInSecondes = (parseInt(ishaTime[0])) * 60 * 60 + (parseInt(ishaTime[1])) * 60;
            if(timeInSeconde > ishaInSecondes){
                diff =(86400 - timeInSeconde ) + toNextInSeconde;
            }
            else{
              diff = timeInSeconde - toNextInSeconde;
            }
        }
        else{
            diff =  toNextInSeconde - timeInSeconde;
        }

        let time_ = this.format((Math.floor(diff / 3600))) + " : " + this.format((Math.floor((diff % 3600) / 60))) + " : " + this.format((diff % 3600 % 60))

        return {
            diff, time_
        }
    }

    static getDAteArabe(salat, date = new Date().getDate()){
        
            let dateData = salat.data[date].date.hijri

            let day = dateData.weekday.ar;
            let day_number = dateData.day;
            let month = dateData.month.ar;
            let year = dateData.year;
            return year +" " +day  +" " + day_number + " " + month;
        
    }
     /**
     * return la priere suivant en object
     */
    nextPriere(){
        let priereTimes_ = [];
    
        for(let priere in this.prieresTimings){
            let a = this.prieresTimings[priere].split(':'); // split it at the colons
            // minutes are worth 60 seconds. Hours are worth 60 minutes.
            let b = (parseInt(a[0])) * 60 * 60 + (+parseInt(a[1]) * 60); 
            let nowInSeconde = 15 * 3600 + 25 * 60;
           if(b > nowInSeconde)
           {
               priereTimes_.push({name: priere, time: this.prieresTimings[0].slice(0,5)})

           }
            
        }
        
        return priereTimes_[priereTimes_.length -3 ]
    }


